@import url(https://fonts.googleapis.com/css?family=Homemade+Apple|IM+Fell+English|VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  background-color: #ebd5b3; }

html {
  font-size: 16px; }
  @media screen and (min-width: 320px) {
    html {
      font-size: calc(16px + 8 * ((100vw - 320px) / 880)); } }
  @media screen and (min-width: 1200px) {
    html {
      font-size: 24px; } }

h1 {
  font-size: 2.0736rem; }
  h1 {
    font-size: 27.648px; }
    @media screen and (min-width: 320px) {
      h1 {
        font-size: calc(27.648px + 26.352 * ((100vw - 320px) / 880)); } }
    @media screen and (min-width: 1200px) {
      h1 {
        font-size: 54px; } }

h2 {
  font-size: 1.728rem; }
  h2 {
    font-size: 27.648px; }
    @media screen and (min-width: 320px) {
      h2 {
        font-size: calc(27.648px + 26.352 * ((100vw - 320px) / 880)); } }
    @media screen and (min-width: 1200px) {
      h2 {
        font-size: 54px; } }

h3 {
  font-size: 1.44rem; }
  h3 {
    font-size: 23.04px; }
    @media screen and (min-width: 320px) {
      h3 {
        font-size: calc(23.04px + 12.96 * ((100vw - 320px) / 880)); } }
    @media screen and (min-width: 1200px) {
      h3 {
        font-size: 36px; } }

.space {
  font-family: 'VT323', monospace; }

.cursive {
  font-family: 'Homemade Apple', cursive; }

.serif {
  font-family: 'IM Fell English', serif; }

.App {
  text-align: center; }

.App-logo {
  height: 40vmin; }

img:first-child {
  /* mix-blend-mode: overlay; */ }

.App-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black; }

.App-link {
  color: #4f7c40; }

